<template>
 <div class="container-fluid">
    <table v-if="miniMail" class="table table-sm table-bordered">
         <tr>
             <td>From: {{miniMail.from}}</td>
             <td>To: {{miniMail.to}}</td>
         </tr>
         <tr>
             <td colspan="2">subject: {{miniMail.subject}}</td>
         </tr>
         <tr>
             <td colspan="2" v-html="`Message: ${miniMail.htmlContent}`"></td>
         </tr>
         <tr>
             <td colspan="2">
                    Attachments: <br>
                 <template v-for="att in miniMail.attachments">
                    <a class="btn btn-primary" :href="att.uri">{{att.name}}</a> &nbsp;
                 </template>
             </td>
         </tr>
     </table>
 </div>
</template>
<script>
import { mapGetters, mapActions,mapMutations } from 'vuex';
export default {
    name: 'MiniMail',
    mounted() {
        if (this.$route.params.id) {
            this.getMiniMailAction({id: this.$route.params.id})
        }
    },
    methods: {
        ...mapActions({
            getMiniMailAction: 'miniMail/getMiniMailAction'
        })
    },
    computed: {
        ...mapGetters({
            miniMail: 'miniMail/miniMailGetter',
        }),
    }
}
</script>